<template>
  <div id="game">
    <div id="game-container" v-if="dataLoaded && !showLog">
      <div id="crafting">
        <div id="crafting-ingredients">
          <div class="ingr" @click="manageIngredient(0)">
            <img :src="getURL(ingredients[0])" alt>
          </div>
          <div class="ingr" @click="manageIngredient(1)">
            <img :src="getURL(ingredients[1])" alt>
          </div>
        </div>
        <div id="crafting-result"></div>
      </div>

      <Inventory
        id="inv"
        :passedInv="inv"
        :passedFocused="focusedIndex"
        @updateFocused="updateFocused"
        @updateInv="updateInv"
      />
    </div>

    <div class="info" v-if="dataLoaded && !showLog">
      <i>Your hero is signed in!</i>
      <span>{{nick}} | {{level}} lvl</span>
      <button class="logoutBtn" @click="logoutUser">Sign out</button>
    </div>

    <div class="log" v-if="dataLoaded && showLog">
      <span style="font-size: 3rem">Welcome, the glorious hero!</span>
      <div class="version">
        <span class="name">v0.1-alpha</span>
        <ul class="changes">
          <li>created initial design</li>
          <li>created basic game mechanics</li>
          <li>created basic register form</li>
          <li>deployed the site</li>
        </ul>
      </div>
      <div class="version">
        <span class="name">v0.2-alpha (current)</span>
        <ul class="changes">
          <li>design corrections (colors, fonts)</li>
          <li>expanded game mechanics to its initial purpose</li>
          <li>created user databases and connected them with auth.</li>
          <li>made America great again</li>
        </ul>
      </div>
      <div class="version" style="border-top: 1px solid black;">
        <ul class="changes">
          <li
            style="font-size: 1.6rem;"
          >Game currently isn't playable due to technical issues and splitting the code to smaller instances. Coming back soon!</li>
        </ul>

        <button class="logoutBtn" @click="logoutUser">Sign out</button>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from "@/components/firebaseInit.js";
import Inventory from "@/components/Inventory.vue";

export default {
  name: "game",
  data() {
    return {
      dataLoaded: false,
      nick: null,
      level: 0,
      ingredients: [null, null],
      inv: [],
      focusedIndex: null,
      showLog: true,

      email: ""
    };
  },
  components: {
    Inventory
  },
  methods: {
    updateFocused(index) {
      console.log("Index updated! ", index);
      this.focusedIndex = index;
    },
    updateInv(inv) {
      console.log("Inventory updated!");

      this.inv = inv;
    },
    manageIngredient(slot) {
      //zdejmowanie itemu z craftingu
      if (this.focusedIndex == null) {
        if (this.ingredients[slot] != null) {
          this.inv[this.ingredients[slot]].isIngredient = false;
          this.ingredients[slot] = null;
        }

        console.log("ZdjÄ™to item");
        return;
      }

      //dodawanie do craftingu
      if (this.ingredients[slot] != null) {
        return;
      }

      const item = this.inv[this.focusedIndex];

      item.focused = false;
      item.isIngredient = true;
      this.ingredients[slot] = this.focusedIndex;
      this.focusedIndex = null;

      console.log("dodano item");
    },
    getURL(index) {
      if (index == null) return null;
      return require("../assets/" + this.inv[index].itemURL + ".png");
    },
    getData() {
      const itemsRef = firebase.db.collection("items");

      const usersRef = firebase.db.collection("users");
      firebase.auth.onAuthStateChanged(user => {
        if (!user) return;

        usersRef
          .doc(user.uid)
          .get()
          .then(doc => {
            if (doc.exists) {
              const data = doc.data();

              this.nick = data.nick;
              this.level = data.level;

              for (let i = 0; i < data.inventory.length; i++) {
                const it = data.inventory[i];

                itemsRef
                  .doc(it)
                  .get()
                  .then(itemDoc => {
                    if (itemDoc.exists) {
                      const itemData = itemDoc.data();
                      itemData["focused"] = false;
                      itemData["isIngredient"] = false;
                      itemData["index"] = i;

                      this.inv.push(itemData);
                    }
                  });
              }
              // data.inventory.forEach(it => {

              // });

              this.dataLoaded = true;
            }
          })
          /* ERROR HANDLING! */
          .catch(err => alert(err));
      });
    },
    logoutUser() {
      firebase.auth.signOut();
    }
  },
  created() {
    this.getData();
  }
};
</script>

<style lang="scss" scoped>
$bg-color: #3f3f3f;
$gc-color: #607d8b;

$main-color: rgb(252, 216, 15);

.log {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

  overflow: auto;

  display: flex;
  flex-direction: column;

  background: yellow;
  max-width: 900px;
  min-width: 400px;

  max-height: 400px;

  border: 4px solid black;
  border-radius: 2rem;
  padding: 1rem;

  font-size: 1rem;
  color: black;

  & > span:nth-child(1) {
    border-bottom: 2px solid black;
    padding: 5px;
  }

  & > div.version {
    padding: 0.5rem;

    & .name {
      font-size: 2.3rem;
      color: crimson;
      border-bottom: 3px double red;
    }

    & ul.changes {
      padding: 0;
      text-align: center;
      list-style-type: none;
      font-style: oblique;
      font-size: 1.2rem;

      & li {
        padding: 0.2rem;
      }
    }
  }
}

#game {
  color: crimson;
  font-size: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background: $bg-color;

  height: 100vh;
  overflow: hidden;
}

#game-container {
  background: $gc-color;
  width: 1100px;
  height: 700px;

  border-radius: 2rem;

  display: flex;
  flex-direction: column;
}

#crafting {
  background: #b83c17;

  height: 30%;

  border-radius: 2rem 2rem 3rem 3rem;

  display: flex;
  align-items: center;
  justify-content: space-around;

  & > #crafting-ingredients {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 50%;

    & > .ingr {
      border: 2px solid gold;

      width: 10rem;
      height: 10rem;

      background-size: cover;

      & img {
        width: 100%;
        pointer-events: none;
        user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        -webkit-user-select: none;
      }
    }
  }

  & > #crafting-result {
    border: 2px solid black;

    width: 10rem;
    height: 10rem;
  }

  // & > #crafting-result, & .ingr {
  //     width: 30%;
  //     height: 100px;
  //     border: 2px solid black;
  // }
}

.info {
  display: flex;
  flex-direction: column;

  position: fixed;
  top: 10px;
  right: 10px;

  background: $main-color;
  color: black;
  font-size: 1.3rem;
  padding: 10px;
  border-radius: 1rem;

  & > span {
    border-top: 1px solid black;
    padding: 5px;
    margin-top: 10px;
    font-weight: 600;
  }
}

.logoutBtn {
  text-decoration: none;
  border: none;
  background: gray;
  color: white;
  margin: 0px auto 20px auto;
  padding: 8px;
  border-radius: 10px;
  font-size: 2rem;

  cursor: pointer;
}
</style>



