<template>
  <div id="inv">
    <div id="info-bubble" :style="itemInfo.css" v-if="itemInfo.show">
      <div id="item-name">{{itemInfo.name}}</div>
      <div id="item-desc">{{itemInfo.desc}}</div>
    </div>
    <div
      class="item"
      v-for="item in inv.filter(i => !i.isIngredient)"
      :class="{focused: item.focused}"
      :style="{borderColor: resolveColor(item.quality)}"
      :key="item.index"
      @click="focusItem(item.index)"
      @mouseenter="showItemInfo(item)"
      @mouseleave="hideItemInfo()"
      @mousemove="updateBubble($event)"
    >
      <img :src="getURL(item.index)">
    </div>
  </div>
</template>

<script>
export default {
  name: "inventory",
  props: ["passedInv", "passedFocused"],
  data() {
    return {
      focusedIndex: this.passedFocused,
      inv: this.passedInv,

      itemInfo: {
        show: false,
        name: null,
        desc: null,

        css: {
          top: 0,
          left: 0
        }
      }
    };
  },
  methods: {
    test() {},
    focusItem(index) {
      if (this.focusedIndex != null) {
        if (this.focusedIndex == index) {
          this.focusedIndex = null;
          this.inv[index].focused = false;
        }

        this.$emit("updateFocused", null);
        this.$emit("updateInv", this.inv);
        return;
      }

      this.inv[index].focused = true;
      this.focusedIndex = index;

      this.$emit("updateInv", this.inv);
      this.$emit("updateFocused", this.focusedIndex);
    },
    getURL(index) {
      if (index == null) return null;
      return require("../assets/" + this.inv[index].itemURL + ".png");
    },

    showItemInfo(item) {
      this.itemInfo.show = true;

      this.itemInfo.name = item.name;
      this.itemInfo.desc = item.desc[0];
    },
    hideItemInfo() {
      this.itemInfo.show = false;
    },
    updateBubble(e) {
      this.itemInfo.css.left = e.clientX + 15 + "px";
      this.itemInfo.css.top = e.clientY + 15 + "px";
    },
    resolveColor(quality) {
      switch (quality) {
        case "common":
          return "darkgray";
          break;

        case "unique":
          return "lime";
          break;

        case "mythic":
          return "orangered";
          break;

        default:
          return "red";
          break;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
$bg-color: #3f3f3f;
$gc-color: #607d8b;

$main-color: rgb(252, 216, 15);

#inv {
  background: $gc-color;
  height: 70%;
  border-radius: 0 0 2rem 2rem;
  overflow: auto;

  display: flex;
  // justify-content: center;
  flex-flow: row wrap;
  align-content: flex-start;
  padding: 1rem;

  // background: rgba($color: #d8d8d8, $alpha: 0.5);

  & > .item {
    width: 7rem;
    height: 7rem;
    border: 2px solid cyan;
    margin: 1rem;
    transition: transform 1s, opacity 1s;

    & img {
      width: 100%;
      pointer-events: none;
      user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      -webkit-user-select: none;
    }

    background-size: cover;
  }
}

#info-bubble {
  position: absolute;
  top: 0;
  left: 10px;

  max-width: 15rem;

  border-radius: 1rem;
  padding: 0.5rem;
  background: rgba($color: #e0dbdb, $alpha: 0.8);
  // visibility: hidden;
  z-index: 2;
  text-align: center;

  & > #item-name {
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 5px;
    padding: 3px;
    border-bottom: 1px solid #3f3f3f;
    color: darkblue;
  }

  & > #item-desc {
    font-size: 1rem;
    font-weight: 300;
    font-style: oblique;
    color: black;
    // text-align: left;
  }
}

.focused {
  position: relative;
  transform: translateY(-20px);
}

.focused::after {
  content: "";
  position: absolute;
  top: calc(100% + 13px);
  left: 0;
  width: 100%;
  height: 0;
  background: #3f3f3f;
  box-shadow: #3f3f3f 0 5px 17px 6px;
  animation: opacityFocused 1s;
}

@keyframes opacityFocused {
  from {
    opacity: 0;
    top: 100%;
  }
  to {
    opacity: 1;
  }
}
</style>



